import React, { useEffect, Suspense } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
// Custom
import Hidden from "@material-ui/core/Hidden";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
// Custom Icons
import HomeIcon from "@material-ui/icons/Home";
import PublicIcon from "@material-ui/icons/Public";
import GitHubIcon from "@material-ui/icons/GitHub";
import ImportContactsIcon from "@material-ui/icons/ImportContacts";
import LinearProgress from "@material-ui/core/LinearProgress";
import {
  //BrowserRouter,
  Route,
  Link,
  Switch,
  //Redirect,
  //useLocation,
} from "react-router-dom";
// Custom Controls
import Switcher from "@material-ui/core/Switch";

const drawerWidth = 190;

// Pages
const Home = React.lazy(() => import("../pages/Home/Home"));
const Contact = React.lazy(() => import("../pages/Contact/Contact"));
const Gists = React.lazy(() => import("../pages/Gists/Gists"));
const GistPost = React.lazy(() => import("../pages/Category/Gists/Post"));
const News = React.lazy(() => import("../pages/Category/News/List"));
const CategoryList = React.lazy(() => import("../pages/Category/List"));
const Post = React.lazy(() => import("../pages/Category/Post"));
const NotFound = React.lazy(() => import("../pages/Errors/NotFound"));

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    backgroundColor: theme.palette.primary.dark,
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  toolbar2: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    //padding: theme.spacing(3),
  },
  listItem: {
    justifyContent: "center",
    paddingTop: 0,
    paddingBottom: 0,
    paddingLeft: theme.spacing(0),
    [theme.breakpoints.down("md")]: {
      paddingLeft: theme.spacing(0),
    },
    [theme.breakpoints.up("md")]: {
      paddingLeft: theme.spacing(0),
    },
    //color: theme.palette.secondary.light
  },
  listItemActive: {
    justifyContent: "center",
    paddingTop: 0,
    paddingBottom: 0,
    backgroundColor: theme.palette.primary.dark,
    "&:hover": {
      backgroundColor: theme.palette.primary.main,
    },
    color: theme.palette.primary.contrastText,
    paddingLeft: theme.spacing(0),
  },

  listItemClosed: {
    justifyContent: "center",
    paddingTop: 0,
    paddingBottom: 0,
    paddingLeft: theme.spacing(3),
  },
  listItemClosedActive: {
    justifyContent: "center",
    paddingTop: 0,
    paddingBottom: 0,
    backgroundColor: theme.palette.primary.dark,
    "&:hover": {
      backgroundColor: theme.palette.primary.main,
    },
    paddingLeft: theme.spacing(3),
  },
  listItemTextOpened: {
    display: "block",
  },
  listItemTextClosed: {
    display: "none",
  },
  iconActive: {
    color: "#fff",
  },
  iconNotActive: {
    color: "#fff",
  },
}));

export default function MiniDrawer() {
  //const theme = useTheme();
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [lightTheme, setLightTheme] = React.useState(false);
  const [currentMenuItemIndex, setCurrentMenuItemIndex] = React.useState(0);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };
  const listItemClassName = (index) => {
    if (open) {
      return index === currentMenuItemIndex
        ? classes.listItemActive
        : classes.listItem;
    } else {
      return index === currentMenuItemIndex
        ? classes.listItemClosedActive
        : classes.listItemClosed;
    }
  };
  const setMenuItemIndex = (index) => {
    setCurrentMenuItemIndex(index);
  };
  const handleMenuItemIcon = (index) => {
    return index === currentMenuItemIndex
      ? classes.iconActive
      : classes.iconNotActive;
  };
  const listItemText = () => {
    if (open) {
      return classes.listItemTextOpened;
    } else {
      return classes.listItemTextClosed;
    }
  };
  useEffect(() => {
    const theme = localStorage.getItem("theme");
    if (theme && theme === "light") {
      setLightTheme(true);
    }
  }, []);
  useEffect(() => {
    console.log("Current theme: " + lightTheme ? "Dark Blue" : "Dark");
  }, [lightTheme]);

  const createTheme = () => {
    return lightTheme === true
      ? createMuiTheme({
          palette: {
            type: "light",
            common: {
              black: "#fff",
              white: "#ddd",
            },
            // action: {
            //   active: "rgba(0, 0, 0, 0.04)",
            // },
            background: {
              paper: "#0f1c24",
              default: "#031f30",
            },
            // primary: {
            //   light: "#ff0000",
            //   main: "#ff0000",
            //   dark: "#ff0000",
            //   contrastText: "#fff",
            // },
            text: {
              primary: "#ffffff",
              secondary: "#00ff00",
              disabled: "#ddd",
              hint: "#fff",
            },
          },
        })
      : createMuiTheme({
          palette: {
            type: "dark",
          },
        });
  };

  const setTheme = () => {
    const light = !lightTheme;
    setLightTheme(light);
    if (localStorage.getItem("theme") !== (light ? "light" : "dark")) {
      localStorage.removeItem("theme");
      localStorage.setItem("theme", light ? "light" : "dark");
    }
  };

  return (
    <ThemeProvider theme={createTheme()}>
      <div className={classes.root}>
        <CssBaseline />
        <Hidden only={["xl", "lg", "md"]}>
          <AppBar
            position="fixed"
            className={clsx(classes.appBar, {
              [classes.appBarShift]: open,
            })}
          >
            <Toolbar>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                onClick={handleDrawerOpen}
                edge="start"
                className={clsx(classes.menuButton, {
                  [classes.hide]: open,
                })}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" noWrap>
                Статьи и заметки
              </Typography>
            </Toolbar>
          </AppBar>
        </Hidden>
        <Drawer
          variant="permanent"
          className={clsx(classes.drawer, {
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          })}
          classes={{
            paper: clsx({
              [classes.drawerOpen]: open,
              [classes.drawerClose]: !open,
            }),
          }}
        >
          <div className={open ? classes.toolbar2 : classes.toolbar}>
            <IconButton
              onClick={handleDrawerOpen}
              style={{ display: open ? "none" : "block" }}
            >
              <MenuIcon style={{ color: "#fff" }} />
            </IconButton>
            <IconButton
              onClick={handleDrawerClose}
              style={{ display: open ? "block" : "none", color: "#fff" }}
            >
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          <List>
            {[
              { title: "Главная", icon: <HomeIcon />, url: "/" },
              { title: "Новости", icon: <PublicIcon />, url: "/news" },
              { title: "Гисты", icon: <GitHubIcon />, url: "/gists" },
              {
                title: "Контакты",
                icon: <ImportContactsIcon />,
                url: "/contacts",
              },
            ].map((item, index) => (
              <ListItem
                button
                key={item.title}
                className={listItemClassName(index)}
                onClick={() => setMenuItemIndex(index)}
                component={Link}
                to={{ pathname: item.url }}
              >
                <ListItemIcon>
                  <IconButton className={handleMenuItemIcon(index)}>
                    {item.icon}
                  </IconButton>
                </ListItemIcon>
                <ListItemText primary={item.title} className={listItemText()} />
              </ListItem>
            ))}
          </List>
          <Divider />
          <div
            style={{
              paddingTop: "10px",
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Switcher
              checked={!lightTheme}
              size="small"
              color="default"
              onClick={() => setTheme()}
            />
          </div>
        </Drawer>
        <main className={classes.content}>
          <Hidden only={["xl", "lg", "md"]}>
            <div className={classes.toolbar} />
          </Hidden>
          <Suspense
            fallback={
              <div
                style={{
                  backgroundColor: "#303030",
                  height: "100vh",
                  textAlign: "center",
                  verticalAlign: "middle",
                }}
              >
                <LinearProgress color="secondary" />
              </div>
            }
          >
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route exact path="/news">
                <News />
              </Route>
              <Route exact path="/gists">
                <Gists />
              </Route>
              <Route exact path="/contacts">
                <Contact />
              </Route>
              <Route path="/category/:category/post/:post/file/:file/name/:name">
                <Post />
              </Route>
              <Route path="/category/:slug">
                <CategoryList />
              </Route>
              <Route path="/gists/:slug">
                <GistPost />
              </Route>
              <Route>
                <NotFound />
              </Route>
            </Switch>
          </Suspense>
        </main>
      </div>
    </ThemeProvider>
  );
}
